import React from "react";
import {
  Card,
  Input,
  Form,
  Layout,
  Space,
  Button,
  PageHeader,
} from "antd";
import { Feature, LoginPage } from "../../types";
import { useQuery } from "react-query3";
import queryClient, { useMutation } from "../utils/queryClient";
import useAppStore from "../context/app/Hooks";
import NotLicensed from "components/pages/notLicensed";
import { SaveOutlined } from "@ant-design/icons";
import DocsButton from "components/docsButton";
import { toRelativeUrl } from "components/utils/utils";

export default function LoginPageSettings() {
  const [form] = Form.useForm();
  const { licensed } = useAppStore();
  const { data: loginPage, isLoading } = useQuery<LoginPage>("/loginPage");
  const { mutate: update, isLoading: isUpdateLoading } = useMutation({
    onSuccess: () => queryClient.invalidateQueries("/loginPage"),
  });

  const isLicensed = licensed(Feature.Automation);

  function onFinish(values) {
    update({
      key: "/loginPage",
      action: "update",
      ...{
        ...loginPage,
        ...values,
      },
    });
  }

  return isLicensed ? (

    <Layout>
      <Layout.Content style={{ padding: 24 }}>
        <PageHeader
          ghost={true}
          title={"Login Page"}
          subTitle={"Setup a custom login page for forms authentication."}
          extra={[
            <DocsButton url="config/login-page" />
          ]} />
        <Space size="large" direction="vertical" style={{ width: "100%" }}>
          <Card bordered={false} loading={isLoading}>
            <Form
              form={form}
              onFinish={onFinish}
              name="main_loginpagesettings_form"
              labelCol={{ span: 8 }}
              wrapperCol={{ span: 16 }}
              labelAlign="left"
              initialValues={{
                ...loginPage
              }}
            >
              {form.getFieldValue("image") && <img src={form.getFieldValue("image")} alt="logo" />}
              <Form.Item
                name="image"
                label="Image"
                tooltip="URL to an image to display on the login page."
              >
                <Input />
              </Form.Item>
              {form.getFieldValue("appBarLogo") && <img src={form.getFieldValue("appBarLogo")} alt="appBarLogo" />}
              <Form.Item
                name="appBarLogo"
                label="App Bar Logo"
                tooltip="URL to an image to display within the app bar."
              >
                <Input />
              </Form.Item>
              <Form.Item
                name="title"
                label="Title"
                tooltip="Title text to display."
              >
                <Input />
              </Form.Item>
              <Form.Item
                name="primaryColor"
                label="Primary Color"
                tooltip="Primary Color."
              >
                <Input />
              </Form.Item>
              <Form.Item
                name="secondaryColor"
                label="Secondary Color"
                tooltip="Secondary Color."
              >
                <Input />
              </Form.Item>
              <Form.Item
                name="headerColor"
                label="Header Color"
                tooltip="Header Color."
              >
                <Input />
              </Form.Item>
              <Form.Item
                name="primaryFontColor"
                label="Primary Font Color"
                tooltip="Primary Font Color."
              >
                <Input />
              </Form.Item>
              <Form.Item
                name="secondaryFontColor"
                label="Secondary Font Color"
                tooltip="Secondary Font Color."
              >
                <Input />
              </Form.Item>
              <Form.Item
                name="headerFontColor"
                label="Header Font Color"
                tooltip="Header Font Color."
              >
                <Input />
              </Form.Item>
              <Form.Item
                name="copyright"
                label="Copyright"
                tooltip="Copyright text to display at the bottom of the page."
              >
                <Input />
              </Form.Item>
              <Form.Item>
                <Space direction="horizontal">
                  <Button
                    loading={isUpdateLoading}
                    type="primary"
                    htmlType="submit"
                    icon={<SaveOutlined />}
                  >
                    Save Changes
                  </Button>
                  <Button href={toRelativeUrl("/login")} target="_blank">
                    View
                  </Button>
                </Space>
              </Form.Item>
            </Form>
          </Card>
        </Space>
      </Layout.Content>
    </Layout>
  ) : (
    <NotLicensed title="Custom login pages are only available for licensed instances." />
  );;
}
